}
.ImageList {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 10px;
  grid-auto-flow: column;
  grid-auto-columns: calc(66% - 40px);
  overflow-x: scroll;
  overflow-y: hidden;
  -webkit-scroll-snap-type: x mandatory;
      -ms-scroll-snap-type: x mandatory;
          scroll-snap-type: x mandatory;
}

@media all and (min-width: 768px) {
  .ImageList {
    grid-auto-columns: calc(50% - 40px);
  }
}

.ImageList::-webkit-scrollbar {
  display: none;
}

.ImageList::before,
.ImageList::after {
  content: '';
  width: 10px;
}

.ImageList-share {
  width: 100%;
  text-align: center;
}

.react-sharing-button__link {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  margin: 0.5em;
  border-radius: 5px;
  -webkit-transition: 50ms ease-out;
  transition: 50ms ease-out;
  padding: 0.5em 0.75em;
  line-height: 1.2em;
}

.react-sharing-button__icon {
  fill: #fff;
  stroke-width: 0;
  width: 1.2em;
  height: 1.2em;
  line-height: 1.2em;
  vertical-align: bottom;
}

.react-sharing-button__text {
  padding-left: 0.4em;
  line-height: 1.2em;
}

.react-sharing-button--download {
  background-color: #dd4b39;
  cursor: pointer;
}

.react-sharing-button--download:hover {
  background-color: #c23321;
}

.react-sharing-button--facebook {
  background-color: #3b5998;
}

.react-sharing-button--facebook:hover {
  background-color: #2d4373;
}

.react-sharing-button--twitter {
  background-color: #55acee;
}

.react-sharing-button--twitter:hover {
  background-color: #2795e9;
}

.Image-container {
  margin: 30px 15px 15px;
	padding: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,.3);
  -webkit-transform: none;
          transform: none;
  scroll-snap-align: center;
}

/* Rotate all even images -2 degrees */
.Image-container:nth-child(2n) {
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
}

/* Rotate every fifth image by 2 degrees */
.Image-container:nth-child(3n) {
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
}

.duotone {
  position: relative;
  display: grid;
}

.duotone::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  bottom: 0px;
  right: 0;
  pointer-events: none;
}

.yellow-red {
  background-color: rgba(221, 204, 0, 1);
}

.yellow-red img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: darken;
}

.yellow-red::after {
  background-color: rgba(221, 0, 0, 1);
  mix-blend-mode: lighten;
  color: white;
}

.green-blue {
  background-color: #00ff36
}

.green-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: multiply;
}

.green-blue::after {
  background-color: #23278a;
  mix-blend-mode: lighten;
  color: white;
}

.purple-green {
  background-color: #88169d;
}

.purple-green img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: lighten;
}

.purple-green::after {
  mix-blend-mode: hard-light;
  background: -webkit-gradient(linear, right bottom, left top, from(#75d775), to(#321a5b));
  background: -webkit-linear-gradient(bottom right, #75d775, #321a5b);
  background: linear-gradient(to top left, #75d775, #321a5b);
}

.pink-blue {
  background-color: #3A34FF;
}

.pink-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: hard-light;
}

.pink-blue::after {
  mix-blend-mode: darken;
  background: #FF49BA;
}

.red-blue {
  background-color: #F7392B;
}

.red-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: multiply;
}

.red-blue::after {
  mix-blend-mode: lighten;
  background: #16006F;
}

.orange-green {
  background-color: #70F134;
}

.orange-green img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: luminosity;
}

.orange-green::after {
  mix-blend-mode: multiply;
  background: #FF8B02;
}

.orange-black {
  background-color: #010101;
}

.orange-black img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: luminosity;
}

.orange-black::after {
  mix-blend-mode: overlay;
  background: #F98900;
}

